import {api} from '../../api/api.js'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			username: '',
			password: ''
		}
	},
	methods: {
		toReg() {
			uni.navigateTo({
				url: '../register/register'
			})
		},
		toForget() {
			uni.navigateTo({
				url: '../forgetPS/forgetPS'
			})
		},
		toLogin() {
			if(this.username === null || this.username === '' ||this.username === undefined) {
				uni.showToast({
					title: "请输入用户名",
					icon:'none',
					duration: 1500
				})
			}else if(this.password === null || this.password === '' ||this.password === undefined) {
				uni.showToast({
					title: "请输入密码",
					icon:'none',
					duration: 1500
				})
			}
			uni.request({
				url:api.login,
				data: {
					username: this.username,
					password: this.password
				},
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				method: "post",
				success: (res) => {
					console.log(res.data.code);
					if (res.data.code === "success") {
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							duration: 1500
						})
						uni.setStorage({
							key: 'session_id',
							data: res.data.result.session_id,
							success: function() {
								console.log(res.data.result.session_id);
								console.log('session_id save');
							}
						})
						getApp().globalData.session_id =res.data.result.session_id;
						uni.setStorage({
							key: 'uid',
							data: res.data.result.uid,
							success: function() {
								console.log(res.data.result.uid);
								console.log('uid save');
							}
						})
						getApp().globalData.uid = res.data.result.uid;
						uni.switchTab({
							url:'../index/index'
						})
						return;
					}else {
						uni.showToast({
							title: res.data.msg,
							icon:'none',
							duration: 1500
						})
						this.password = '';
					}
				}
			});
		},
	}
}
